import React, {Component} from 'react';
import {Route, Switch} from 'react-router-dom';

import {
  PersonalData, UserEmail, VerifyEmail,
  UserPhone, CountrySelect, VerifyPhone
} from './featutes';
import {withContext} from 'services/with-context';

const Context = React.createContext();

export class InitialUserData extends Component {
  state = {
    firstName: null,
    lastName: null,
    dateOfBirth: null,
    email: null,
    phoneCode: '+380',
    phone: '930014212',
    country: null,
    photo: null
  };

  setField = (fieldName, fieldValue) => {


    this.setState({
      [fieldName]: fieldValue
    });
  };

  setPersonalData = (firstName, lastName, dateOfBirth) => {
    this.setState({
      firstName,
      lastName,
      dateOfBirth
    });
  };

  render() {
    const url = this.props.match.url;

    return (
     <Context.Provider
      value={{fields: {...this.state}, setField: this.setField, setPersonalData: this.setPersonalData}}
     >
       <Switch>
         <Route exact path={url} component={withContext(PersonalData, Context)}/>
         <Route path={`${url}/email`} component={withContext(UserEmail, Context)}/>
         <Route path={`${url}/verify-email`} component={withContext(VerifyEmail, Context)}/>
         <Route path={`${url}/phone`} component={withContext(UserPhone, Context)}/>
         <Route path={`${url}/country`} component={withContext(CountrySelect, Context)}/>
         <Route path={`${url}/verify-phone`} component={withContext(VerifyPhone, Context)}/>
       </Switch>
     </Context.Provider>
    );
  }
}


