import React, {Component} from 'react';

import {Wrapper, ScreenTitle, ScreenDesc, TextInput, InputField} from '../ui';
import {SmallPrimaryButton} from 'ui';

export class PersonalData extends Component {
  state = {
    firstName: '',
    lastName: '',
    dateOfBirth: ''
  };

  onClick = () => {
    this.props.setPersonalData(
     this.state.firstName,
     this.state.firstName,
     this.state.dateOfBirth,
    );

    this.props.history.push(`${this.props.match.url}email`);
  };

  handleChange = ({target}) => {
    this.setState({
      [target.name]: target.value
    });
  };

  render() {
    return (
     <Wrapper>
       <ScreenTitle>Add your personal data</ScreenTitle>
       <ScreenDesc>We’ll send you a text message to verify</ScreenDesc>
       <InputField margin='40px 0 7px'>
         <TextInput onChange={this.handleChange} name='firstName'>First Name</TextInput>
       </InputField>
       <InputField>
         <TextInput onChange={this.handleChange} name='lastName'>Last Name</TextInput>
       </InputField>
       <InputField>
         <TextInput type='date' onChange={this.handleChange} name='dateOfBirth'>Date Of Birth</TextInput>
       </InputField>
       <SmallPrimaryButton onClick={this.onClick} background='#6743DF' margin='auto 0 20px'>Continue</SmallPrimaryButton>
     </Wrapper>
    );
  }
}


