import React, {Component} from 'react';
import styled from 'styled-components';

import {Wrapper, SearchInput, InputField} from '../ui';
import countries from 'services/countries.json';
import backArrowImage from '../assets/back-arrow.svg';
import {Button} from 'ui';

const BackButton = Button.extend`
  position: absolute;
  width: 20px;
  height: 20px;
  top: 20px;
  left: 16px;
  background: url(${backArrowImage}) no-repeat center;
`;

const Title = styled.h3`
  width: 100%;
  text-align: left;
  font-size: 28px;
  color: #504A8E;
  letter-spacing: -0.68px;
  line-height: 42px;
`;

const CountriesList = styled.ul`
  list-style: none;
  width: 100%;
  
  li {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    color: #767FAB;
    letter-spacing: 0.4px;
    line-height: 24px;
    margin: 15px 0;
  }
`;

const CountryName = styled.div`
  max-width: 70%;
`;

export class CountrySelect extends Component {
  state = {
    searchQuery: ''
  };
  
  onChange = ({target}) => {
    this.setState({
      searchQuery: target.value
    });
  };

  getFilteredCountries() {
    return countries.filter(item => item.name.toLowerCase().search(this.state.searchQuery.toLowerCase()) >= 0);
  }

  onClick = ({currentTarget}) => {
    this.props.setField('phoneCode', currentTarget.dataset.code);
    this.props.setField('country', currentTarget.dataset.name);
    this.props.history.goBack();
  };

  render() {
    return (
     <Wrapper>
       <Title>Country</Title>
       <InputField>
         <SearchInput onChange={this.onChange}/>
       </InputField>
       <CountriesList>
         {this.getFilteredCountries().map((item, index) => (
          <li key={index} data-name={item.name} data-code={item.code} onClick={this.onClick}>
            <CountryName>{item.name}</CountryName>
            <div>{item.code}</div>
          </li>
         ))}
       </CountriesList>
       <BackButton onClick={this.props.history.goBack}/>
     </Wrapper>
    );
  }
}


