import React, { Component } from 'react';
import styled from 'styled-components';

import {Wrapper, LabelText} from './input';

const Code = styled.span`
  position: absolute;
  bottom: 3px;
  background-color: #6743DF;
  padding: 1px 5px;
  left: 0;
  color: #FFFFFF;
  font-weight: 800;
  letter-spacing: 0.4px;
  line-height: 24px;
  border-radius: 2px;
`;

const PhoneWrapper = Wrapper.extend`
  input {
    padding: 3px 0 3px ${p => p.paddingLeft}px;  
  }
`;

export class PhoneInput extends Component {
  constructor() {
    super();

    setTimeout(() => {
      this.setState({
        flag: true
      });
    }, 0);

    this.codeElement = null;
  }

  state = {
    flag: false
  };

  getLeftPadding = () => {
    return (this.codeElement ? this.codeElement.clientWidth : 37) + 5
  };

  render() {
    return (
     <PhoneWrapper paddingLeft={this.getLeftPadding()}>
       <input type="number" onChange={this.props.onChange}/>
       <LabelText>{this.props.children}</LabelText>
       <Code innerRef={el => this.codeElement = el} onClick={this.props.onSelectCountry}>{this.props.code}</Code>
     </PhoneWrapper>
    );
  }
}


