import React from 'react';
import styled from 'styled-components';

import searchImage from '../../../assets/search.svg';

const InputWrapper = styled.label`
  width: 100%;
  position: relative;
  display: block;

  input {
   width: 100%;
   padding: 8px 0 8px 40px;
   border: none;
   border-radius: 6px;
   background-color: rgba(118, 127, 171, .1); 
   font-size: 16px;
   color: #504A8E;
   letter-spacing: 0.4px;
   line-height: 24px;
   font-weight: 700;
   outline: none;
   
   &::placeholder {
     font-size: 14px;
     color: #767FAB;
     letter-spacing: 0;
     line-height: 22px;
     font-weight: 400;
   }
  }
  
  &:before {
    content: '';
    display: block;
    width: 16px;
    height: 16px;
    position: absolute;
    left: 16px;
    bottom: 12px;
    background: url(${searchImage}) no-repeat center;
  }
`;

export const SearchInput = ({onChange}) => (
    <InputWrapper>
      <input onChange={onChange} type="text" placeholder='Search'/>
    </InputWrapper>
);


