import styled from 'styled-components';

export const LabelText = styled.span`
  order: 1;
  font-size: 12px;
  color: #767FAB;
  letter-spacing: 0.3px;
  line-height: 18px;
  text-transform: uppercase;
`;

export const Wrapper = styled.label`
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  
  input {
    width: 100%;
    order: 2;
    outline: none;
    border: none;
    border-bottom: 1px rgba(118, 127, 171, .12) solid;
    transition: all .1s ease-in-out;
    background: white;
    font-size: 16px;
    color: #504A8E;
    letter-spacing: 0.4px;
    line-height: 24px;
    font-weight: 700;
    
    &:focus {
      border-bottom-color: #6743DF;
    }
  }
`;


