import React from 'react';

import {Wrapper, LabelText} from './input';

export const TextInput = ({children, onChange, name, id = name, type = 'text'}) => (
    <Wrapper>
      <input name={name} type={type} onChange={onChange}/>
      <LabelText>{children}</LabelText>
    </Wrapper>
);


