import React, {Component} from 'react';

import {Wrapper, ScreenTitle, ScreenDesc, PhoneInput, InputField} from '../ui';
import {SmallPrimaryButton} from 'ui';

export class UserPhone extends Component {
  state = {
    phone: '',
  };

  onClick = () => {
    this.props.history.push('verify-phone');
  };

  onSelectCountry = () => {
    this.props.history.push('country');
  };

  handleChange = ({target}) => {
    this.setState({
      phone: target.value
    });
  };

  render() {
    return (
     <Wrapper>
       <ScreenTitle>Add your phone number</ScreenTitle>
       <ScreenDesc>We’ll send you a text message to verify</ScreenDesc>
       <InputField margin='15vh 0 7px'>
         <PhoneInput
          onSelectCountry={this.onSelectCountry}
          onChange={this.handleChange}
          code={this.props.fields.phoneCode}
          type='number'
         >Phone number</PhoneInput>
       </InputField>
       <SmallPrimaryButton onClick={this.onClick} background='#6743DF' margin='auto 0 20px'>Verify</SmallPrimaryButton>
     </Wrapper>
    );
  }
}


