import React, {Component} from 'react';
import styled from 'styled-components';

import {Wrapper, ScreenTitle, ScreenDesc, ScreenData} from '../ui';
import {PincodeDisplay, NumberKeyboard, TextButton} from 'ui';

const VerifyWrapper = Wrapper.extend`
  padding-bottom: 0;
`;

const ResendButton = TextButton.extend`
  margin-top: 5px;
  font-size: 12px;
  color: #6743DF;
  letter-spacing: 0;
  line-height: 22px;
`;

const PinDesc = styled.div`
  margin-top: 10px;
  font-size: 12px;
  color: #767FAB;
  letter-spacing: 0;
  line-height: 22px;
`;

const PinText = styled.div`
  margin-top: 25px;
  padding: 0 25px;
  font-size: 12px;
  color: #767FAB;
  text-align: center;
  line-height: 18px;
`;

export class VerifyPhone extends Component {
  state = {
    pin: [],
    pinLength: 6
  };

  onClickKeyboard = (num) => {
    if (this.state.pin.length < this.state.pinLength) {
      const pin = [...this.state.pin, num];

      this.setState({
        pin
      }, () => {

        pin.length === this.state.pinLength && this.props.history.push('phone');
      });
    }
  };

  onKeyboardDelete = () => {
    if (this.state.pin.length > 0) {
      this.setState({
        pin: this.state.pin.slice(0, this.state.pin.length - 1)
      });
    }
  };

  render() {
    return (
     <VerifyWrapper>
       <ScreenTitle>Please check your messages</ScreenTitle>
       <ScreenDesc>We’ll send you an email to verify</ScreenDesc>
       <ScreenData>{this.props.fields.phoneCode + this.props.fields.phone}</ScreenData>
       <PincodeDisplay value={this.state.pin} length={this.state.pinLength} background='rgba(103, 67, 223, .2)'/>
       <PinDesc>Enter your 6-digit code</PinDesc>
       <PinText>We have sent you a phone number. Please enter the code you
         received, so that we can verify that this is your phone number</PinText>
       <ResendButton>Resend Code</ResendButton>
       <NumberKeyboard onClick={this.onClickKeyboard} onDelete={this.onKeyboardDelete}/>
     </VerifyWrapper>
    );
  }
}


