import React, {Component} from 'react';

import {Wrapper, ScreenTitle, ScreenDescription} from '../ui';
import {PincodeDisplay, NumberKeyboard} from 'ui';

const PinDesc = ScreenDescription.extend`
  text-align: center;
  font-weight: 800;
`;

export class Pincode extends Component {
  state = {
    pin: [],
    retypePin: [],
    length: 6,
    hide: false
  };

  onClick = (num) => {
    const {pin, retypePin, length: pinLength} = this.state;

    if (pin.length < pinLength) {
      this.setState({
        pin: [...pin, num]
      });
    } else if (retypePin.length < pinLength) {
      this.setState((state, props) => {
        const retype = [...state.retypePin, num];

        if (retype.length < pinLength) {
          return {
            retypePin: retype
          }
        } else {
          if (pin.join('') === retype.join('')) {
            this.props.history.push('/initial-user-data');

            return {
              retypePin: retype
            };
          } else {
            console.log('false');

            return {
              pin: [],
              retypePin: []
            };
          }
        }
      });
    }
  };

  onDelete = () => {
    if (this.state.pin.length < this.state.length) {
      if (this.state.pin.length > 0) {
        this.setState({
          pin: this.state.pin.slice(0, this.state.pin.length - 1)
        });
      }
    } else {
      if (this.state.retypePin.length > 0) {
        this.setState({
          pin: this.state.pin.slice(0, this.state.pin.length - 1)
        });
      }
    }
  };

  toggleHide = () => {
    this.setState({
      hide: !this.state.hide
    });
  };

  displayPin = () => {
    return this.state.pin.length < this.state.length ?
     this.state.pin : this.state.retypePin
  };

  render() {
    return (
     <Wrapper>
       {this.state.pin.length < this.state.length ? (
        <ScreenTitle>Create your PIN code</ScreenTitle>
       ) : (
        <ScreenTitle>Retype your PIN code</ScreenTitle>
       )}
       <ScreenDescription>Bitcoin is different - it cannot be safely held w</ScreenDescription>
       <PincodeDisplay value={this.displayPin()} length={this.state.length} hide={this.state.hide}/>
       <PinDesc>Enter your pin</PinDesc>
       <NumberKeyboard onClick={this.onClick} onDelete={this.onDelete} toggleHide={this.toggleHide}/>
     </Wrapper>
    );
  }
}


