import React, {Component} from 'react';

import {Wrapper, ScreenTitle, ScreenDesc, TextInput, InputField} from '../ui';
import {SmallPrimaryButton} from 'ui';

export class UserEmail extends Component {
  state = {
    email: ''
  };

  onClick = () => {
    this.props.setField('email', this.state.email);
    this.props.history.push('verify-email');
  };

  handleChange = ({target}) => {
    this.setState({[target.name]: target.value});
  };

  render() {
    return (
     <Wrapper>
       <ScreenTitle>Add your email address</ScreenTitle>
       <ScreenDesc>We’ll send you an email to verify</ScreenDesc>
       <InputField margin='15vh 0 7px'>
         <TextInput onChange={this.handleChange} name='email'>Email</TextInput>
       </InputField>
       <SmallPrimaryButton onClick={this.onClick} background='#6743DF' margin='auto 0 20px'>Verify</SmallPrimaryButton>
     </Wrapper>
    );
  }
}


