import React from 'react';
import styled from 'styled-components';

import del from 'assets/images/del.svg';
import eye from 'assets/images/eye.svg';

const KeyboardWrapper = styled.div`
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  margin-top: auto;
  padding: 10px;
  background-color: white;
`;

const KeyboardItem = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
  flex-basis: 33.3%;
  font-size: 28px;
  color: #504A8E;
  letter-spacing: 0;
  text-align: center;
  transition: all .2s;
  user-select: none;
  outline: none;
  
  &:active {
    background-color:#f6f6f6;
  }
`;

const NumberKeyboard = ({onClick, onDelete, toggleHide, hideBtn}) => (
 <KeyboardWrapper>
   <KeyboardItem onClick={() => onClick(1)}>1</KeyboardItem>
   <KeyboardItem onClick={() => onClick(2)}>2</KeyboardItem>
   <KeyboardItem onClick={() => onClick(3)}>3</KeyboardItem>
   <KeyboardItem onClick={() => onClick(4)}>4</KeyboardItem>
   <KeyboardItem onClick={() => onClick(5)}>5</KeyboardItem>
   <KeyboardItem onClick={() => onClick(6)}>6</KeyboardItem>
   <KeyboardItem onClick={() => onClick(7)}>7</KeyboardItem>
   <KeyboardItem onClick={() => onClick(8)}>8</KeyboardItem>
   <KeyboardItem onClick={() => onClick(9)}>9</KeyboardItem>
   <KeyboardItem tabIndex='1' onBlur={toggleHide} onFocus={toggleHide}>
     {hideBtn && <img src={eye} alt=""/>}
   </KeyboardItem>
   <KeyboardItem onClick={() => onClick(1)}>0</KeyboardItem>
   <KeyboardItem onClick={onDelete}>
     <img src={del} alt=""/>
   </KeyboardItem>
 </KeyboardWrapper>
);

NumberKeyboard.defaultProps = {
  hideBtn: false
};

export {NumberKeyboard};


