import React from 'react';
import styled from 'styled-components';

const PincodeNumber = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 58px;
  margin: 0 4px;
  border-radius: 6px;
  color: white;
  font-weight: 800;
  font-size: 18px;
  
  &:nth-child(3) {
    margin-right: 10px;
  }
  &:nth-child(4) {
    margin-left: 10px;
  }
`;

const PincodeDisplayWrapper = styled.div`
  display: flex;
  margin-top: 100px;
  
  ${PincodeNumber} {
    background-color: ${p => p.background};
  }
`;

const PincodeDisplay = ({value, length, hide, ...props}) => (
 <PincodeDisplayWrapper {...props}>
   {[...new Array(+length).keys()].map(index => (
    <PincodeNumber key={index}>{value[index] && hide ? '*' : value[index] || ''}</PincodeNumber>
   ))}
 </PincodeDisplayWrapper>
);

PincodeDisplay.defaultProps = {
  value: [],
  length: 6,
  hide: false,
  background: 'rgba(255, 255, 255, .08)'
};

export {PincodeDisplay};


